import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main.component';

const routes: Routes = [
  { path: '', component: MainComponent,
  children: [
      { path: 'usuarios', loadChildren: () => import('./pages/user/user.module').then(m => m.UserModule) },
      { path: 'empresas', loadChildren: () => import('./pages/company/company.module').then(m => m.CompanyModule) },
      { path: 'tiendas', loadChildren: () => import('./pages/store/store.module').then(m => m.StoreModule) },
      { path: 'publicaciones', loadChildren: () => import('./pages/article/article.module').then(m => m.ArticleModule) },
      { path: 'promociones', loadChildren: () => import('./pages/offers/offers.module').then(m => m.OfferModule) },
      { path: 'servicios', loadChildren: () => import('./pages/service/service.module').then(m => m.ServiceModule) },
      { path: 'citas', loadChildren: () => import('./pages/appointment/appointment.module').then(m => m.AppointmentModule) },
      { path: 'anuncios', loadChildren: () => import('./pages/announcement/announcement.module').then(m => m.AnnouncementModule)}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MainRoutingModule { }
